import React, { useEffect, useState } from "react";
// COMPONENTS
import styled from "styled-components";
import HeroSection from "./HeroSection";
import MainGridLayout from "./styles/MainGridLayout";
import { i18n } from "src/i18n";
import Breadcrumb from "../shared/Breadcrumb";
import AproposService from "src/modules/Apropos/AproposService";
function Mediatheque() {
  const langue = localStorage.getItem("language");
  const [apropos, setApropos] = useState("");
  useEffect(() => {
    AproposService.list("", "", "", "").then((res) => {
      if (langue === "fr") {
        setApropos(res.rows[0].mediatequeDescFR);
      }
      if (langue === "ar") {
        setApropos(res.rows[0].mediatequeDescAR);
      }
      if (langue === "en") {
        setApropos(res.rows[0].mediatequeDescEN);
      }
    });
  }, []);
  return (
    <Media>
      <Breadcrumb
        title={i18n("entities.mediatique.label")}
        items={[[i18n("app.title"), "/"], [i18n("entities.mediatique.label")]]}
      />
      <DescriptionSection>
        <p>{apropos}</p>
      </DescriptionSection>
      <HeroSection />
      <MainGridLayout />
    </Media>
  );
}

const Media = styled.div`
  @media (max-width: 1071px) {
    margin: 160px auto 0 auto;
  }
`;
const DescriptionSection = styled.div`
  color: var(--violet);
  font-size: 1rem;
  font-weight: 400;
  text-align: justify;
  margin: 28px 0 10px 210px;
`;

export default Mediatheque;
