import React from "react";
import styled from "styled-components";
import moment from "moment";
import ReactPlayer from "react-player";

// ICONS
import { BiMicrophone } from "react-icons/bi";
import Translate from "../../shared/Translate";
import EntityPlayerButton from "../styles/EntityPlayerButton";
import { i18n } from "src/i18n";
import { Link } from "react-router-dom";

export default function AudioPlayer({ dataSource }) {
  // HELPER FUNCTIONS
  const pipeDate = (date) => {
    let d = date.split("T")[0];
    return moment(d).format("L");
  };

  return (
    <AudioPlayerLayout key={dataSource._id} className="videoCardVertical">
      <Link to={"/Mediatheque/entity/" + dataSource._id}>
        {dataSource.photos?.length > 0 ? (
          <div
            className="cardThumbnail"
            style={{
              backgroundImage: "url(" + dataSource.photos[0].downloadUrl + ")",
            }}
          >
            {/* <EntityPlayerButton data={dataSource} entity="mainGridPlayer" /> */}
          </div>
        ) : (
          <div className="cardThumbnail hasNoThumbnail">
            {/* <EntityPlayerButton data={dataSource} entity="mainGridPlayer" /> */}
          </div>
        )}
        <div className="dateAndType">
          <div className="contentType" style={{ width: "inherit" }}>
            <BiMicrophone className="icon" />
            {i18n(`entities.mediatique.enumerators.type.${dataSource.type}`)}
          </div>
          <span className="contentDate">{pipeDate(dataSource.updatedAt)}</span>
        </div>
        <div className="verticalPlayerTitle text__wrap">
          <span style={{ textTransform: "lowercase" }}>
            {Translate.Trans("title", dataSource)}
          </span>
        </div>
        {dataSource?.link && (
          <ReactPlayer url={dataSource?.link} width={280} height={10} />
        )}
      </Link>
    </AudioPlayerLayout>
  );
}

const AudioPlayerLayout = styled.div`
  position: relative;
  height: inherit !important;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .cardThumbnail {
    width: 100%;
    height: 200px;
    background-size: cover;
    background-position: center;
    position: relative;
  }
  .hasNoThumbnail {
    background-image: url("https://imgur.com/N1ZiTM4.jpeg");
  }
  .dateAndType {
    margin-top: 8px;
    height: 30px;
    color: #fff;

    display: flex;
    align-items: center;
    justify-content: space-between;

    span {
      color: #fff;
    }

    .contentType {
      width: 100px;
      border: 1px solid red;
      border-radius: 0.5rem;
      height: 30px;

      font-family: "Proxima Nova";
      font-style: normal;
      font-weight: 400;
      font-size: 1rem;
      text-transform: capitalize;
      line-height: 15px;
      color: red;
      padding: 0.3rem 0.5rem;

      display: flex;
      align-items: center;
      justify-content: space-evenly;

      .icon {
        margin-right: 5px;
      }
    }

    .contentDate {
      color: rgba(122, 122, 122, 1);
      font-size: 12px;
      font-family: "Proxima Nova";

      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
    }
  }

  .verticalPlayerTitle {
    margin-top: 1rem;
    height: 5px;

    span {
      width: 100%;
      font-family: "Proxima Nova";
      text-transform: capitalize !important;
      line-height: 1.5;
      font-weight: bold;
      font-size: 16px;
      color: var(--violet);

      overflow: hidden;
      display: inline-block;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
`;
