import React, { useEffect, useState } from "react";
import styled from "styled-components";
import MediathequeService from "src/modules/mediatheque/MediathequeService";

// COMPONENTS
import Videos from "../sub-sections/Videos";
import Photos from "../sub-sections/Photos";
import Podcasts from "../sub-sections/Podcasts";
import Docs from "../sub-sections/Docs";
import VoirPlusButton from "../shared/VoirPlusButton";

// ICONS/Assets
import Skeleton from "react-loading-skeleton";

export default function MainGridLayout() {
  // vids
  const [videos, setVideos] = useState([]);
  const [videoIsLoading, setVideoIsLoading] = useState(true);

  // photos
  const [photos, setPhotos] = useState([]);
  const [photoIsLoading, setPhotoIsLoading] = useState(true);

  // podcast
  const [podcast, setPodcats] = useState([]);
  const [podcastIsLoading, setPodcastIsLoading] = useState(true);

  // documentaire
  const [docs, setDocs] = useState([]);
  const [docIsLoading, setDocIsLoading] = useState(true);

  // exposition
  const [exposition, setExposition] = useState([]);
  const [expositionIsLoading, setExpositionIsLoading] = useState(true);

  // reportage
  const [reportage, setReportage] = useState([]);
  const [reportageIsLoading, setReportageIsLoading] = useState(true);
  const getLatestContent = () => {
    let videosCOunt = 0;
    let audioCOunt = 0;
    let photosCOunt = 0;
    let docsCOunt = 0;
    let expoCOunt = 0;
    let reportCOunt = 0;

    MediathequeService.getLatestMediatheques().then((value) => {
      // getting videos only
      value.rows?.map((entry, index) => {
        if (entry.type === "videos") {
          if (videosCOunt < 1) {
            setVideos((videos) => videos.concat(entry));
            videosCOunt += 1;
          }
        }

        if (entry.type === "autres") {
          if (photosCOunt < 1) {
            setPhotos((photos) => photos.concat(entry));
            photosCOunt += 1;
          }
        }

        if (entry.type === "podcast") {
          if (audioCOunt < 1) {
            setPodcats((podcast) => podcast.concat(entry));
            audioCOunt += 1;
          }
        }

        if (entry.type === "documentaire") {
          if (docsCOunt < 1) {
            setDocs((docs) => docs.concat(entry));
            docsCOunt += 1;
          }
        }
        if (entry.type === "exposition") {
          if (expoCOunt < 1) {
            setExposition((exposition) => exposition.concat(entry));
            expoCOunt += 1;
          }
        }
        if (entry.type === "reportage") {
          if (reportCOunt < 1) {
            setReportage((reportage) => reportage.concat(entry));
            reportCOunt += 1;
          }
        }
      });
      setVideoIsLoading(false);
      setPhotoIsLoading(false);
      setPodcastIsLoading(false);
      setDocIsLoading(false);
      setExpositionIsLoading(false);
      setReportageIsLoading(false);
    });
  };

  useEffect(() => {
    getLatestContent();
  }, []);

  return (
    <MainContentLayout>
      <section className="mainContentLeftSection">
        {videoIsLoading ? (
          <Skeleton height={300} />
        ) : (
          <section>
            {videos.length > 0 ? (
              <section>
                <Videos videosList={videos} />
                <VoirPlusButton contentType="videos" />
              </section>
            ) : null}
          </section>
        )}
        {photoIsLoading ? (
          <Skeleton height={300} />
        ) : (
          <section>
            {photos.length > 0 ? (
              <section>
                <Photos photosList={photos} />
                <VoirPlusButton contentType="photos" />
              </section>
            ) : null}
          </section>
        )}
        {reportageIsLoading ? (
          <Skeleton height={300} />
        ) : (
          <section>
            {reportage.length > 0 ? (
              <section>
                <Photos photosList={reportage} />
                <VoirPlusButton contentType="reportage" />
              </section>
            ) : null}
          </section>
        )}
      </section>
      {/* LEFT SECTION */}
      <section className="mainContentRightSection">
        {docIsLoading ? (
          <Skeleton height={300} />
        ) : (
          <section>
            {docs.length > 0 ? (
              <section>
                <Docs docstList={docs} />
                <VoirPlusButton contentType="documentaires" />
              </section>
            ) : null}
          </section>
        )}
        {podcastIsLoading ? (
          <Skeleton height={300} />
        ) : (
          <section>
            {podcast.length > 0 ? (
              <section>
                <Podcasts podcastList={podcast} />
                <VoirPlusButton contentType="podcasts" />
              </section>
            ) : null}
          </section>
        )}
        {expositionIsLoading ? (
          <Skeleton height={300} />
        ) : (
          <section>
            {exposition.length > 0 ? (
              <section>
                <Photos photosList={exposition} />
                <VoirPlusButton contentType="exposition" />
              </section>
            ) : null}
          </section>
        )}
      </section>
    </MainContentLayout>
  );
}

const MainContentLayout = styled.section`
  margin: 1rem auto;
  width: var(--cerntered-content);
  min-height: 200px;
  position: relative;
  top: -180px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;

  .mainContentRightSection {
    width: 350px !important;
    margin-left: 2rem;
  }

  .mainContentLeftSection {
    width: 350px !important;
    margin-right: 2rem;
  }

  @media (max-width: 767px) {
    display: block;
    width: 100%;
    padding: 0 0.5rem;

    .mainContentRightSection {
      width: 100% !important;
      padding: 0;
      margin: 0;
    }
    .mainContentLeftSection {
      width: 100% !important;
      padding: 0;
      margin: 0;
    }
  }
`;
